<template>
  <div class="novel-link">
    <div v-if="$route.name === '小说链接'">
      <Button type="primary" size="large" to="/manage/links/novel/create" class="create" style="margin-bottom: 30px;">
        新增小说链接
      </Button>
      <Button
        size="large"
        style="margin-bottom: 30px; float: left;"
        @click="
          selectState = true
          channel = currentChannel
        ">
        {{currentChannel}}
      </Button>
      <Modal
        v-model="selectState"
        title="选择渠道"
        @on-ok="selectChannel"
        @on-cancel="cancelSelect"
      >
        <RadioGroup v-model="channel" type="button" @on-change="changeChannel">
          <Radio class="radio-channel" label="默认">默认</Radio>
          <Radio class="radio-channel" v-for="item in channelList" :label="item.name">{{item.name}}</Radio>
        </RadioGroup>
      </Modal>
      <Table class="novel-table" :columns="novelHeader" :data="novelData"></Table>
      <Page show-total :current="1" :total="count" :page-size="20" @on-change="index => show(channel, index)"/>
    </div>
    <router-view />
  </div>
</template>

<script>
  import Clipboard from "clipboard";

  new Clipboard(".copyBtn");
  export default {
    data() {
      return {
        novelData: [],
        domain_names: [],
        selectState: false,
        currentChannel: '默认',
        channel: "默认",
        novelHeader: [
          {
            key: "id",
            title: "id",
            align: "center"
          },
          {
            title: "类型",
            align: "center",
            render: (h, params) => {
              let type =
                params.row.type == 0
                  ? "微跳"
                  : params.row.type == 1
                  ? "非微调单页"
                  : "非微调多页";
              return h(
                "span",
                {
                  props: {}
                },
                type
              );
            }
          },
          {
            key: "name",
            title: "公号",
            align: "center"
          },
          {
            title: "图片",
            align: "center",
            render: (h, params) => {
              let src = this.domain_names[0].domain_name + params.row.picurl;
              return h("img", {
                attrs: {
                  src: src,
                  style: "width: 100%;padding-top: 5px;"
                }
              });
            }
          },
          {
            key: "channel",
            title: "渠道",
            align: "center"
          },
          {
            key: "remarks",
            title: "备注",
            align: "center"
          },
          {
            title: "复制链接",
            align: "center",
            render: (h, params) => {
              let that = this;
              return h(
                "Button",
                {
                  class: "copyBtn",
                  attrs: {
                    "data-clipboard-text":
                      that.domain_names[0].domain_name +
                      "/tuiguang/" +
                      (params.row.type == 0
                        ? "weitiao/"
                        : params.row.type == 1
                          ? "singlepage/"
                          : "multipage/") +
                      params.row.id
                  },
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.copy();
                    }
                  }
                },
                "复制链接"
              );
            }
          },
          {
            title: "Action",
            align: "center",
            width: 240,
            render: (h, params) => {
              let that = this;
              return h("div", [
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.$router.push({
                          name: "修改小说链接",
                          params: {
                            row: params.row,
                            index: params.index,
                            domain_name: that.domain_names[0].domain_name
                          }
                        });
                      }
                    }
                  },
                  "修改"
                ),
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "error",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.deleteOne(params.row, params.index);
                      }
                    }
                  },
                  "删除"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "info",
                      size: "small"
                    },
                    on: {
                      click() {
                        let row = params.row;
                        row.id += '_copy';
                        that.$axios.post("/tuiguang/novel/add", row).then(res => {
                          if (res.data.err) {
                            that.$Message.info(res.data.err);
                          } else {
                            that.$Message.info('复制成功');
                            that.show();
                          }
                        });
                      }
                    }
                  },
                  "复制"
                ),
              ]);
            }
          }
        ],
        count: 100,
        channelList: []
      };
    },
    mounted() {
      this.show();
      this.getChannelList()
    },
    methods: {
      selectChannel() {
        this.currentChannel = this.channel;
        this.show(this.currentChannel, null);
        this.channel = "默认"
      },
      cancelSelect() {
        this.channel = "默认"
      },
      changeChannel(channel) {
        this
      },
      getChannelList() {
        this.$axios.get('/channel')
          .then(res => {
            if(res.data.success) {
              this.channelList = res.data.data
            }
          })
      },
      deleteOne(row, index) {
        let that = this;
        this.$Modal.confirm({
          title: "提示:",
          content: "确认删除这条内容吗？",
          onOk: () => {
            that.$axios
              .post("/tuiguang/novel/delete_one", {id: row.id})
              .then(res => {
                that.$Message.info(res.data.message);
                that.novelData.splice(index, 1);
              });
          }
        });
      },
      copy() {
        this.$Message.info("复制成功");
      },
      show(channel, page) {
        channel === '默认' && (channel = '');
        this.$axios.get("/tuiguang/novel/show",{params: {page: page || 1, channel: channel || ''}}).then(res => {
          this.novelData = res.data.data;
          this.count = res.data.count;
          this.domain_names = res.data.domain_names;
        });
      },
      goTop() {
        let self = this;
        self.timer = setInterval(function () {
          let osTop = document.documentElement.scrollTop || document.body.scrollTop;
          let ispeed = Math.floor(-osTop);
          document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed;
          self.isTop = true;
          if (osTop === 0) {
            clearInterval(self.timer)
          }
        }, 30)
      }
    },
    watch: {
      $route(to, from) {
        if (this.$route.params.state !== "cancel") {
          if (from.name === "创建小说链接") {
            this.novelData.unshift(this.$route.params);
          } else if (from.name === "修改小说链接") {
            this.novelData.splice(
              this.$route.params.index,
              1,
              this.$route.params.row
            );
          }
        }
      }
    }
  };
</script>

<style scoped>
  .radio-channel {
    margin: 0 10px 10px 0;
  }
</style>
